<template>
	<div>
		<van-nav-bar class="hader" title="主题详情" left-text="返回" left-arrow @click-left="back()">
		  <a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS!=true&&isWeixin!=true">
		    <van-button type="primary" size="small">安装APP</van-button>
		  </a>
		  <a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS==true&&isWeixin!=true">
		    <van-button type="primary" size="small">安装APP</van-button>
		  </a>
		  <a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect" slot="right" v-if="isWeixin==true">
		    <van-button type="primary" size="small">关注</van-button>
		  </a>
		</van-nav-bar>
		<div class="food-img mt35"
		     :style="{backgroundImage:'url('+osshost+ themeDetail.topicimg +detailpic+')'}"></div>
		<div class="cont mt10" style="margin-top: 0.5rem;">
		  <!--左边区域-->
		    <!--封面图-->
		    <div class="menu_info mt10">
		      <div class="name text-cut-two">{{ themeDetail.title }}</div>
		      <div class="info clearfix">  
		        <div class="grayColor">
		          <span><van-icon class="icon-like2 grayColor ml10" name="eye" >{{themeDetail.visits}}</van-icon></span>
		        </div>
		      </div>
		    </div>
			<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS!=true&&isWeixin!=true">
			  <van-button class="wb100 mt10 mb10" type="primary">收藏主题到手机</van-button>
			</a>
			<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS==true&&isWeixin!=true">
			  <van-button class="wb100 mt10 mb10" type="primary">收藏主题到手机</van-button>
			</a>
			  <div v-if="isWeixin==true" class="gzh-guanzhu">
				  <img src="../assets/img/qr.jpg"/>
				  <span>长按识别二维码，关注管饭美食</span>
			  </div>
		    <!--用户信息-->
		    <div>
		      <ul class="dr_list">
		        <li class="clearfix mb15">
		          <div class="left headicon bg-img br50"
		               :style="{backgroundImage:'url('+osshost+ themeDetail.faceimg +userface+')'}">
		          </div>
		          <div class="info">
		            <h2 class="text-bold text-df text-16">{{themeDetail.nickname}}</h2>
					<span class="grayColor text-14">{{themeDetail.pushtime}}</span>
		          </div>
				   <i class="icon-like van-icon van-icon-like text-right right mt10" :class="themeDetail.iscollectioned==true?'text-red':''" @click="changeCollection()">{{ themeDetail.collections }}</i>
		        </li>
		      </ul>
		      <div class="text-df">{{themeDetail.introduction}}</div>
		    </div>
		    <!--  推荐菜谱    -->
		    <div class="mt20">
		      <h2>推荐菜谱</h2>	  
			  <div class="food-list1 mt10">
			  	<ul>
			  		<li v-for="(item,index) in themeDetail.cplist" :key="index" @click="bookDetail(item.recipeid)">
			  			<div class="foodlist-img" :style="{ backgroundImage: 'url(' + osshost +item.cpimg +listpic+ ')' }"></div>
			  			<div class="food-list1-tit text-cut">{{ item.title }}</div>
			  			<div class="user">
			  				<img :src="osshost + item.userfaceimg +userface" />
			  				<span>{{item.nickName}}</span>
			  				<i class="icon-like2">被收藏次数: {{ item.cpcollections }}</i>
							<div class="mt10 text-12" v-if="item.reason!=''">
								<span>推荐理由:</span>
								<span>{{ item.reason }}</span>
							</div>
			  			</div>
			  		</li>
			  	</ul>
			  </div>	  
		    </div>
		</div>
	</div>
</template>

<script>
	import qs from 'qs';
	import axios from 'axios';
	export default {
		name: "themeinfo",
		data() {
		  return {
		    num: 13,
		    sortList: '',
		    themeDetail: '',
		    osshost: 'https://guanfan.oss-cn-hangzhou.aliyuncs.com/',
		    // osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
		    listpic: '?x-oss-process=style/list-img',
		    userface: '?x-oss-process=style/user-img',
		    detailpic: '?x-oss-process=style/detail-img',
			isWeixin:'',
			isiOS:'',
			token:''
		  }
		},
		created() {
		  this.themeid=this.$route.query.id;
		  this.token = this.$store.state.user.token || '';
		  this.getDataInfo(this.themeid);
		  this.getwx();
		  this.appSource();
		},
		methods: {
			getwx(){
						var ua = window.navigator.userAgent.toLowerCase(); 
						  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
						    this.isWeixin=true;
						  } else {
						   this.isWeixin=false;
						  } 
			},
			appSource() {
						const u = navigator.userAgent, app = navigator.appVersion;
						if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
							this.isiOS=true;
						} else {
							this.isiOS=false;
						}
			},
		  getDataInfo(themeid) {
			  // getThemeDetail(themeid)
			  // 	.then(res => {
					// if(res.data.success){
					// 	this.themeDetail = res.data.result;
					// } 
			  // 	})
			  // 	.catch(error => {
			  // 		console.log(error);
			  // 	});
				let data = qs.stringify({
					topicid: themeid
				});
				axios({
					method: 'post',
					url: '/subject/topicdetail',
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded',
						'token':this.token
					},
					data
				}).then(response => {
					if (response.data.success) {
						this.themeDetail = response.data.result;
					}
				});
		  },
		  changeCollection(){
		  	if(this.token==""){
		  			this.$router.push({ path: '/pswLogin'});
		  	}else{
		  		if(this.themeDetail.iscollectioned==true){
		  					let data = qs.stringify({
		  						itemid:this.themeid,
		  						itemtype:3
		  					});
		  					axios({
		  						method: 'post',
		  						url: '/action/uncollection',
		  						headers: {
		  							'Content-Type': 'application/x-www-form-urlencoded',
		  							'token':this.token
		  						},
		  						data
		  					}).then(response => {
		  						if (response.data.success) {
		  							this.themeDetail.iscollectioned=false;
		  							this.themeDetail.collections--
		  						}
		  					});
		  			// setUncollection(this.cpid,2).then(res=>{
		  			// 	if(res.data.success){
		  			// 		this.menuDetail.iscollectioned=0;
		  			// 	    this.menuDetail.collections--
		  			// 	}
		  			// })
		  		}else{
		  					let data = qs.stringify({
		  						itemid:this.themeid,
		  						itemtype:3
		  					});
		  					axios({
		  						method: 'post',
		  						url: '/action/collection',
		  						headers: {
		  							'Content-Type': 'application/x-www-form-urlencoded',
		  							'token':this.token
		  						},
		  						data
		  					}).then(response => {
		  						if (response.data.success) {
		  							this.themeDetail.iscollectioned=true;
		  							this.themeDetail.collections++
		  						}
		  					});
		  			// setcollection(this.cpid,2).then(res=>{
		  			// 	if(res.data.success){
		  			// 		this.menuDetail.iscollectioned=1;
		  			// 	    this.menuDetail.collections++
		  			// 	}
		  			// })
		  		}
		  	}
		  },
		back(){
		        this.$router.go(-1);//返回上一层
		    },
			bookDetail(id) {
			  this.$router.push({path: "/cookbookinfo",query:{id:id}})
			}
		}
	}
</script>
<style>
	.van-icon-like:before{
		display: inline-block;
		position: relative;
	}
</style>